import * as THREE from 'three'
import { Stats } from 'three-stats'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import TexturePool from '../FrontPage4.0/TexturePool'

class FrontPageManager {
    container: HTMLElement
    renderer: THREE.WebGLRenderer
    scene: THREE.Scene
    stats: Stats

    // camera
    camera: THREE.PerspectiveCamera
    controls: OrbitControls

    // loading manager
    loadingManager: THREE.LoadingManager

    testObj: THREE.Mesh

    constructor(_container: HTMLElement,
        onLoad?: () => void,
        onProgress?: (url: string, loaded: number, total: number) => void,
        onError?: (url: string) => void,
        onCardFocus?: (index: number) => any,
        onCardRouteJump?: (url: string) => any
    ) {
        // *********** basic settings ***********
        this.container = _container
        this.camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
        this.renderer = new THREE.WebGLRenderer( {alpha: true, antialias: true} )
        this.renderer.setClearColor( 0xffffff )
        this.renderer.setPixelRatio( window.devicePixelRatio )
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        this.renderer.domElement.style.position = 'absolute'
        this.renderer.domElement.style.top = '0px'
        this.renderer.domElement.style.left = '0px'
        this.container.appendChild( this.renderer.domElement )
        this.controls = new OrbitControls(this.camera, this.container)
        
        this.stats = new Stats()
        this.stats.domElement.style.zIndex = '100'
        this.container.appendChild( this.stats.domElement )
        this.camera.position.set(100, 100, 100)

        this.scene = new THREE.Scene()

        // loading manager
        this.loadingManager = new THREE.LoadingManager(() => {
            if (onLoad) {
                onLoad()
                // this.stackCard()
            }
        }, (url: string, loaded: number, total: number) => {
            if (onProgress) {
                onProgress(url, loaded, total)
            }
        }, (url) => {
            if (onError) {
                onError(url)
            }
        })
        
        // light
        const light = new THREE.DirectionalLight(0xffffff)
        light.position.set(0, 500, 200)
        this.scene.add(light)
        // // this.scene.add( new THREE.CameraHelper( light.shadow.camera ) )


        // *********** test obj ***********
        const tx = new THREE.TextureLoader(this.loadingManager).load('checkerboard.png')
        const mat1 = new THREE.MeshBasicMaterial({
            map: tx,
            side: THREE.FrontSide
        })
        const testGeometry = new THREE.PlaneGeometry( 50, 50 )
        this.testObj = new THREE.Mesh( testGeometry, mat1 )
        this.testObj.castShadow = false
        this.testObj.receiveShadow = true
        this.scene.add( this.testObj );
        
        
        window.addEventListener( 'click', this.onClick )

        this.animate()
    }

    onClick = () => {
        const tx = new THREE.TextureLoader(this.loadingManager).load('title.png')
        if (this.testObj.material instanceof THREE.MeshBasicMaterial) {
            this.testObj.material.map = tx
        }
    }

    animate = () => {
        requestAnimationFrame( this.animate )
        this.controls.update()
        this.render()
        this.stats.update()
    }

    render ()
    {
        this.renderer.render(this.scene, this.camera)
    }

    onWindowResize = () => {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.renderer.setSize( window.innerWidth, window.innerHeight )
    }

}

export { FrontPageManager }