

























import Vue from 'vue'
import { FrontPageManager } from '../scripts/TestScene'

type Data = {
    fpm: FrontPageManager | null,
    loadingStat: {
        loaded: number,
        total: number,
        isLoaded: boolean,
        noLoadingStat: boolean
    }
}

export default Vue.extend({
    data(): Data {
        return {
            fpm: null,
            loadingStat: {
                loaded: 0,
                total: 1,
                isLoaded: false,
                noLoadingStat: false
            }
        }
    },
    mounted() {
        const self = this
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            this.fpm = new FrontPageManager(
                this.$refs['canvas-container'],
                () => {
                    console.log('loaded')
                    self.loadingStat.isLoaded = true
                    setTimeout(() => {
                        self.loadingStat.noLoadingStat = true
                    }, 501)
                },
                (_, loaded, total) => {
                    this.loadingStat.loaded = loaded
                    this.loadingStat.total = total
                },
                (url) => {
                    console.error('failed to load asset: ' + url)
                    self.loadingStat.isLoaded = true
                },
                (index) => {
                    console.log('focused on card: ', index)
                }
            )

        }
    },
    methods: {
    }
})
